

















































































































import { Vue, Component } from "vue-property-decorator";
import SvgPhone from "@/assets/svgs/phone3.vue";
import SvgMail from "@/assets/svgs/envelope3.vue";
import SvgDownload from "@/assets/svgs/arrow-alt-to-bottom.vue";
import fileCv from "@/assets/download/CV Trần Quang Đức.jpg";

@Component({
  components: {
    SvgPhone,
    SvgMail,
    SvgDownload
  }
})
export default class App extends Vue {
  container!: HTMLDivElement;

  fileCv = fileCv;

  mounted() {
    this.container = this.$refs.container as HTMLDivElement;
    document.addEventListener("wheel", this.scrollZoom);
    document.addEventListener("mousemove", this.zoom);
    this.container.style.zoom = String(
      Math.round(window.devicePixelRatio * 100) / 100
    );
  }
  destroyed() {
    document.removeEventListener("wheel", this.scrollZoom);
    document.removeEventListener("mousemove", this.zoom);
  }

  scrollZoom(e: WheelEvent) {
    if (!e.ctrlKey) return;
    this.zoom();
  }
  zoom() {
    setTimeout(() => {
      this.container.style.zoom = String(
        Math.round(window.devicePixelRatio * 100) / 100
      );
    }, 1);
  }
}
